.project-imgs {
  display: flex;
  justify-content: center;
  width: 90%;
  ul {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: space-around;
    .project-img {
      display: flex;
      align-items: center;
      width: 100%;

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
  }
}
